const imageContext = require.context(
  "../assets/images", // 图片目录路径
  true, // 扫描子目录
  /\.(png|jpe?g|svg|webp)$/i // 匹配图片格式‌
);

const images = imageContext.keys().reduce((acc, path) => {
  const fileName = path
    .split("/")
    .pop()
    .replace(/\.[^/.]+$/, "") // 移除扩展名
    .toLowerCase();
  acc[fileName] = imageContext(path); // 生成键值对
  return acc;
}, {});

export default images;
